ul {
    list-style: none;
    padding: 10px; }
  
  .nav-list {
    border-left: 1px dotted;
    padding: 0 10px;
    text-transform: uppercase;
    text-decoration: none; }
  
  .nav-item {
    font-family: 'Noto Serif', serif;
    font-size: 12px;
    font-weight: 300;
    color: #f66d52;
    letter-spacing: .1em;
    text-decoration: none; }
  
  .back-to-top {
    height: 20px;
    width: 20px;
    position: fixed;
    right: 50px;
    bottom: 50px; }
  
  .back-to-top .fa {
    font-size: 20px;
    color: #E0A892; }
  
  body {
    font-family: 'Noto Serif', serif;
    height: 100%;
    overflow: auto; }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Roboto', sans-serif; }
  
  h1 {
    margin: 0 auto 20px;
    color: #7FC4BB;
    font-size: 36px;
    font-weight: 100;
    line-height: 32px; }
  
  h2 {
    margin: 1em 0 .5em 0;
    color: #7FC4BB;
    font-size: 30px;
    font-weight: 100; }
  
  h3 {
    margin: 0 auto 20px;
    color: #999;
    font-size: 22px;
    font-weight: 100; }
  
  h4 {
    margin: 0 auto 20px;
    color: #999;
    font-size: 18px;
    font-weight: 100; }
  
  h5 {
    margin: 0 auto 20px;
    color: #999;
    font-size: 16px;
    font-weight: 100; }
  
  h6 {
    margin: 0 auto 20px;
    color: #999;
    font-size: 14px;
    font-weight: 100; }
  
  p, a li, span {
    font-family: 'Noto Serif', serif; }
  
  p {
    margin: 0 auto 20px;
    color: #555; }
  
  label {
    color: #84CDC2; }
  
  a {
    color: #f66d52; }
  
  a:hover {
    color: #f66d52;
    text-decoration: none; }
  
  .form-input {
    border-radius: 0;
    margin: 0 auto 20px;
    color: #555555;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 100; }
  
  .link-icon, .link-text {
    color: #f66d52;
    text-decoration: none;
    margin: 0 10px 0 0; }
  
  .link-text {
    border-bottom: 1px solid rgba(241, 140, 121, 0.25);
    transition: border-bottom-color 0.2s ease-in; }
    .link-text:hover {
      border-bottom-color: #f18c79; }
  
  .link-button {
    height: 35px;
    width: 100px;
    display: inline-block;
    color: #f66d52;
    font-weight: 300;
    text-align: center;
    text-decoration: none;
    background-color: transparent;
    border: 1px solid #f66d52;
    border-radius: 0;
    margin-top: 10px;
    transition-duration: 0.2s; }
    .link-button:hover {
      color: #fff;
      font-weight: 300;
      background-color: #f66d52;
      border: 1px solid #f66d52; }
  
  html {
    overflow: hidden;
    height: 100%; }
  
  .container-inner {
    max-width: 992px;
    margin: 0 auto; }
  
  .section-hero {
    background-color: #F2F2F2;
    min-height: 200px; }
    @media (min-width: 992px) {
      .section-hero .section-hero-inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100vh; } }
  
  .section-main {
    background-color: #F8F8F8; }
    @media (min-width: 992px) {
      .section-main {
        height: 100vh;
        overflow-y: scroll; } }
  
  .u-padded {
    margin: 0 10px;
    padding-top: 10px;
    padding-bottom: 20px; }
    @media (min-width: 992px) {
      .u-padded {
        margin: 0 20px;
        padding-top: 20px;
        padding-bottom: 50px; } }
  
  .u-border {
    border-bottom: 1px solid #c0c0c0; }
  